import React, { Component } from 'react'
import Comment from './Comment'
import AddNewComment from './AddNewComment'
import EditArticle from './EditArticle'

export default class Article extends Component {
  state = {
    open: false,
    openArticle: false,
    showInterfaceComment: false,
    editArticle: false,
  }

  openClose = () => this.setState({ open: !this.state.open })

  showHideCommentInterface = () => {
    this.setState({ showInterfaceComment: !this.state.showInterfaceComment })
  }

  startEditArticle = i => {
    this.setState({ editArticle: true });
    this.props.editArticle(i)
  }

  finishEditArticle = () => this.setState({ editArticle: false })

  render() {
    const { title, description, text, comments } = this.props.it
    const { i, removeComment, addComment, removeArticle } = this.props

    const newText = text.split('\n').map((it, i) => <p key={i}>{it}</p>)

    return (
      <>
        {!this.state.editArticle ?
          <div className='article'>

            <div className="article-manage">
              <button
                onClick={() => this.startEditArticle(i)}
                className="article-remove"
              >
                edit
          </button>
              <button
                className="article-remove"
                onClick={() => removeArticle(this.props.i)}
              >
                remove
          </button>
            </div >

            <h2 className='article-title'>{title}</h2>
            <p className='article-description'>{description}</p>

            {this.state.open ?
              <>
                <div className='article-text-open'>{newText}</div>

                <div className='comments-wrapper col-lg-7 col'>
                  <div className="comments-title">Comments:</div>
                  {comments.map((it, y) => {
                    return <Comment
                      key={y}
                      idComment={y}
                      idArticle={i}
                      author={it.author}
                      commentText={it.commentText}
                      removeComment={removeComment}
                    />
                  })}

                  {!this.state.showInterfaceComment ?
                    <button
                      className='comment-new-button'
                      onClick={() => this.showHideCommentInterface()}>
                      add new comment
                    </button>
                    :
                    <div className='comment-new'>
                      <AddNewComment
                        i={i}
                        addComment={addComment}
                        showHideCommentInterface={this.showHideCommentInterface}
                      />
                    </div>
                  }

                </div>
              </>
              : null
            }

            <div className='row comment-down'>
              <div className="col-sm-6 col-12">
                <div className="count-comments">comments: {comments.length}</div>
              </div>

              <div className="col-sm-6 col-12">
                <button className="learn-more" onClick={() => this.openClose()} >
                  <div className="circle">
                    <span className="icon arrow"></span>
                  </div>
                  <p className="button-text">{this.state.open ? 'close' : 'read more'}</p>
                </button>
              </div>
            </div>
          </div>

          :

          <EditArticle
            i={i}
            CurrentArticleEdit={this.props.CurrentArticleEdit}
            applyEditArticle={this.props.applyEditArticle}
            finishEditArticle={this.finishEditArticle}
          />

        }

      </>
    )
  }
}
