import React, { Component } from 'react'

export default class AddNewComment extends Component {
  state = {
    author: '',
    commentText: '',
    clsTextarea: 'comment-text-area'
  }

  updateAuthor = e => this.setState({ author: e.target.value });
  updateCommentText = e => this.setState({ commentText: e.target.value });

  add = (i, name, text) => {
    if (text !== '') {
      this.props.addComment(i, name, text)
      this.props.showHideCommentInterface()
      this.setState({ clsTextarea: 'comment-text-area' });
      this.setState({ author: '', commentText: '' });
    } else this.setState({ clsTextarea: 'comment-text-area comment-text-area-red' });
  }

  render() {
    return (
      <div>
        <div className="comment-add-title">New Comment</div>
        <div className="comment-add-author">
          <input
            className="comment-add-author-input"
            type="text"
            value={this.state.author}
            onChange={(e) => this.updateAuthor(e)} />
          <h4 className="comment-add-author-title">Name</h4>
        </div>
        <textarea
          className={this.state.clsTextarea}
          type="text"
          value={this.state.commentText}
          onChange={(e) => this.updateCommentText(e)} />
        <button
          className='comment-new-button comment-new-button-sub'
          onClick={() => this.add(this.props.i, this.state.author, this.state.commentText)}
        >
          submit
          </button>
      </div >
    )
  }
}
