import React, { Component } from 'react'

export default class Comment extends Component {
  render() {
    const { idComment, idArticle, author, commentText, removeComment } = this.props;

    return (
      <div className='comment'>
        <h5 className="comment-autor">{author}</h5>
        <p className='comment-text'>{commentText}</p>
        <button className="comment-remove" onClick={() => removeComment(idArticle, idComment)} >
          remove
        </button>
      </div>
    )
  }
}
